<template>
	<div class="login_box">
		<header>
			<Headnav :head_title='loginName.name' head_left="" head_right="切换城市" >
				<router-link to="/city" tag="span" slot="left">
					<van-icon name="arrow-left" />
				</router-link>
			</Headnav>
		</header>
		<div class="login_cont">
			<div class="login_search">
				<input v-model="searchName" type="text" placeholder="  输入学校、商务楼、地址">
				<br>
				<button @click="getData()">提交</button>
			</div>
			<div class="login_txt">
				<div v-if="loginArr==0" class="login_del">
					<p class="login_text">搜索历史</p>
					<ul>
						<li @click="getCont(item)" v-for="(item,index) in Element" :key="index">
							<router-link to="/cart">
								<P class="login_tit">{{item.name}}</P>
								<P>{{item.address}}</P>
							</router-link>
						</li>
					</ul>
					<p class="login_delTxt">
						<span @click="del()">清空历史</span>
					</p>
				</div>
				<div class="login_Search" v-else>
					<ul>
						<li @click="getCont(item)" v-for="(item,k) in loginArr" :key="k">
							<router-link to="/cart">
								<P class="login_tit">{{item.name}}</P>
								<P>{{item.address}}</P>
							</router-link>
						</li>
					</ul>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import Headnav from '../About/headNav'
	export default {
		name: "login",
		data(){
			return{
				loginName:'',
				searchName:'',
				loginArr:[],
				Element:'',
				indexId:''
			}
		},
		components:{
			Headnav
		},
		methods:{
			getData(){
				this.$axios.get(`http://elm.cangdu.org/v1/pois?type=search&city_id=${this.indexId}&keyword="${this.searchName}"`).then(res =>{
					console.log(res)
					this.loginArr= res.data
				})
			},
			//清空本地存储中的数据
			del(){
				this.Element=''
				localStorage.setItem('Element',JSON.stringify(this.Element))
			},
			getCont(item){
				if(this.Element==''){
					//如果我数组中没有值，我第一次添加一个进去
					let arr = []
					arr.push(item)
					localStorage.setItem('Element',JSON.stringify(arr))
				}else{
					//我本地有值
						var flag=false
					//设置一个开关作为判断 当我的flag=true的时候代表我本地存储中有相同的值，不进行添加
					for(let i=0; i<this.Element.length;i++){
						if(this.Element[i].name==item.name){
								flag=true
						}
					}
					//当flag==false的时候带表我本地存储中没有相同的值就进行添加
					if(flag==false){
						this.Element.push(item)
						localStorage.setItem('Element',JSON.stringify(this.Element))
					}
				}
			},
		},
		mounted(){
		 	this.$axios.get('http://elm.cangdu.org/v1/cities/'+ this.$route.query.login).then(res=>{
				this.loginName =res.data
				this.indexId = res.data.id
			    this.$store.commit('getId',res.data.id)
			})
			this.getData()
			if(localStorage.getItem("Element")){
				this.Element =JSON.parse(localStorage.getItem("Element"))
			}
		}
	}
</script>
